import CKEditor from "ckeditor4-react";
import React, { Component, Fragment } from "react";
import classNames from "classnames";

class Index extends Component {
  constructor(props) {
    super(props);
    this.onChange = this.onChange.bind(this);
  }

  onChange(e) {
    const data = e.editor.getData();
    this.props.handleChange(data);
  }

  onBlur(e) {
    // console.log("onBlur event called with event info: ", e);
  }

  afterPaste(e) {
    // console.log("afterPaste event called with event info: ", e);
  }

  render() {
    const { content, isFirstChange } = this.props;
    return (
      <Fragment>
        <CKEditor data={content} onChange={this.onChange} />
        <div
          className={classNames(
            "invalid-feedback",
            content === "" && !isFirstChange && "d-block"
          )}
        >
          Required
        </div>
      </Fragment>
    );
  }
}

export default Index;
